<template>
  <!--  -->
  <div>


    <div class="topTitle-wrap">
      <div>
        <span class="tpTitle">投放报告</span>
      </div>
      <div class="" style="margin-right: 20px">
        <ul class="myDatePicker" slot="mySolt">
          <li class="myDatePicker_li">
            <span>日期范围</span>
            <DatePicker @on-change="startTime" :value="dateRange" type="daterange" placeholder=""
                        placement="bottom-end"
                        :clearable=false :editable=false
                        style="width: 200px;"></DatePicker>
          </li>
          <li class="myDatePicker_li">
            <Button class="btnColor" type="primary" icon="archive" @click="confrimDownLoad">下载报告</Button>
          </li>
        </ul>
      </div>
    </div>

    <RadioGroup v-model="selectMode" type="button" size="large" style="margin: 20px" @on-change="changeReport">
      <Radio label="订单报告"></Radio>
      <Radio label="广告报告"></Radio>
      <Radio label="物料报告"></Radio>
    </RadioGroup>

    <div class="reportContent">
      <div id="main"></div>
      <div class="ract-wrap">
        <div>
          <span class="showRact"></span>
          <span>展现量</span>

        </div>
        <div class="ract-wrap">
          <span class="clickRact"></span>
          <span>点击量</span>

        </div>
      </div>

      <div class="searchReport" v-show="isCode">

        <div style="display: flex;margin: 5px">
        </div>

        <div style="align-self: center;margin-right: 10px;margin: 5px">
          <Input v-model="nameLike" :placeholder="hit" style="width: 300px"></Input>
          <Button class="btnColor" @click="getFilterData" type="primary" icon="search">搜索</Button>

        </div>

      </div>

      <Table border :columns="orderColumns" :data="adReportList" v-show="isOder"></Table>
      <Table border :columns="adColumns" :data="adReportList" v-show="isAd"></Table>
      <Table border :columns="materialColumns" :data="adReportList" v-show="isMaterial"></Table>

      <Page :total="totalCount" :current="pageNo" show-sizer show-total @on-change="changePageNo"
            @on-page-size-change="changePageSize"></Page>
    </div>


  </div>


</template>
<style scoped>


  .searchReport {
    background: #f7f7f7;
    display: -webkit-flex; /* Safari */
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;

  }

  .reportContent {
    margin-left: 20px;
    margin-right: 20px;
  }

  .top-wrap li {
    text-align: center;
    /*border: 1px solid #d7f0f2;*/
    padding: 10px;
    color: #c5c5c5;
  }

  #main {
    width: 90%;
    height: 400px;
  }

  .showRact, .clickRact {
    display: inline-block;
    width: 30px;
    height: 15px;
  }

  .showRact {
    background: #044b7c;
  }

  .clickRact {
    background: #ea1107;
  }

  .ract-wrap {
    text-align: center;
    margin-bottom: 10px;
  }

  .ract-wrap div {
    display: inline-block;

  }

  .ract-wrap div span {
    display: inline-block;
    vertical-align: middle;
  }

  .ract-wrap div:nth-child(1) {
    margin-right: 100px;
  }

  .myDatePicker, .myDatePicker_li {
    display: inline-block;
    color: black;
  }

  .myDatePicker {

  }

  .myDatePicker_li {

  }

  .orderName {
    display: inline-block;
    margin-left: 30px;
  }

  .orderId {
    display: inline-block;
    width: 50px;
  }

  .fu {
    color: black;
  }

  .ivu-page {
    text-align: right;
    margin-right: 30px;
    padding: 10px 10px;
  }

</style>
<script>
  import topTitle from '@/components/common/topTitle'
  import echarts from 'echarts'

  export default {
    components: {topTitle},
    data() {
      return {
        adClickNum: '',
        adShowNum: '',
        adRate: '',
        startDay: '',
        endDay: '',
        dateRange: [],
        pageSize: 10,
        pageNo: 1,
        codePageSize: 10,
        codePageNo: 1,
        codeTotalPage: 1,
        totalCount: 0,
        dataClickNum: [],
        dataShowNum: [],
        dataAdDateDay: [],
        codeGroupId: '',
        searchName: "",
        codeGroupType: "",
        codeId: '',
        nameLike: '',
        modeType: 1,
        isCode: true,
        isCodeGroup: false,
        isOder: true,
        isAd: false,
        isMaterial: false,
        isCodeGroup: false,
        modelGroup: "全部",
        textLoadMore: '加载更多',
        selectMode: '订单报告',
        myChart: '',
        codeGroupArr: [],
        codeArr: '',
        hit: '请输入订单名称',
        getCodeList: [],
        orderColumns: [
          {
            title: '订单名称',
            key: 'showName'
          },
          {
            title: '订单ID',
            key: 'showId'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          },
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          },
          {
            title: '点击率',
            key: 'rate'
          }
        ],
        adColumns: [
          {
            title: '广告名称',
            key: 'showName'
          },
          {
            title: '广告ID',
            key: 'showId'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          }
          ,
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          },
          {
            title: '点击率',
            key: 'rate'
          }
        ],
        materialColumns: [
          {
            title: '广告名称',
            key: 'showName'
          },
          {
            title: '物料ID',
            key: 'showId'
          },
          {
            title: '展现量',
            key: 'showNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.showNum))
            }
          },
          {
            title: '点击量',
            key: 'clickNum',
            render: (h, params) => {
              return h('span', this.splitNum(params.row.clickNum))
            }
          },
          {
            title: '点击率',
            key: 'rate'
          }
        ],
        adReportList: []

      }
    },
    created() {

    },
    mounted() {
      this.startDay = this.getPreDate(-7);//获取一周的时间
      this.endDay = this.getPreDate(-1);
      this.dateRange = [this.startDay, this.endDay];
      this.myChart = echarts.init(document.getElementById('main'));

      this.selectReport();

    },
    methods: {

      initEcharts() {

        var option = {
          title: {
            text: ''
          },

          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          color: ["#044b7c", "#ea1107"],


          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.dataAdDateDay,
          },
          yAxis: [
            // type: 'value'
            {
              name: '展现量',
              type: 'value'
            },
            {
              name: '点击量',
              type: 'value',

            }
          ],
          minInterval: 1,
          series: [
            {
              name: '展现量',
              data: this.dataShowNum,
              type: 'line',
              areaStyle: {
                color: '#d6eaff',
              },
              lineStyle: {
                color: '#044b7c',
              },

            },
            {
              name: '点击量',
              data: this.dataClickNum,
              yAxisIndex: 1,
              type: 'line',
              areaStyle: {
                color: '#ffd6e1',
              },
              lineStyle: {
                color: '#ea1107',
              },


            }
          ]
        };

        // 使用刚指定的配置项和数据显示图表。
        this.myChart.setOption(option);
      },
      splitNum(num) {
        var reg = /(\d)(?=(?:\d{3})+$)/g;
        var dec = (num + '').split('.');
        if (dec.length == 2) {
          return dec[0].replace(reg, '$1,') + '.' + dec[1];
        } else {
          return dec[0].replace(reg, '$1,');
        }
      },

      getOrderReportList() {
        const url = `/report/getOrderReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.adReportList = [];
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            chartList.forEach((item, index) => {
              this.adReportList.push(item);
            })

          }
        });
      },
      getOrderReportChart() {
        const url = `/report/getOrderReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          // console.log(res)
          if (res.body.status == 0) {
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay);

            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            //this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },
      getDeliverReportList() {
        const url = `/report/getDeliverReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.adReportList = [];
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            chartList.forEach((item, index) => {
              this.adReportList.push(item);
            })

          }
        });
      },
      getDeliverReportChart() {
        const url = `/report/getDeliverReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay);

            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            // this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },

      getMaterialReportList() {
        const url = `/report/getMaterialReportList.json?startDay=${this.startDay}&endDay=${this.endDay}&pageSize=${this.pageSize}&pageNo=${this.pageNo}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.adReportList = [];
            let chartList = res.body.data.result;
            this.totalCount = res.body.data.totalCount;
            chartList.forEach((item, index) => {
              this.adReportList.push(item);
            })

          }
        });
      },
      getMaterialReportChart() {
        const url = `/report/getMaterialReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}&nameLike=${encodeURIComponent(this.nameLike)}`;
        this.$Http.get(url, {}).then(res => {
          //console.log(res)
          if (res.body.status == 0) {
            this.dataAdDateDay = [];
            this.dataClickNum = [];
            this.dataShowNum = [];
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay);

            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;
            //this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();
          }
        });
      },


      getPreDate(days) {
        let date = new Date();//获取当前时间
        date.setDate(date.getDate() + days);//设置天数 -1 天
        return date.Format("yyyy-MM-dd");
      },

      selectReport() {

        if (this.modeType == 1) {
          this.getOrderReportList();
          this.getOrderReportChart();
        }
        else if (this.modeType == 2) {

          this.getDeliverReportList();
          this.getDeliverReportChart();
        }
        else {
          this.getMaterialReportChart();
          this.getMaterialReportList();
        }
      },
      confrimDownLoad() {
        const token = sessionStorage.getItem('token');

        var param = `?startDay=${this.startDay}&endDay=${this.endDay}&nameLike=${encodeURIComponent(this.nameLike)}&t=${token}`;
        if (this.modeType == 1) {
          window.location.href = this.ApiUrl + `/report/downloadOrderReport.json` + param;

        }
        else if (this.modeType == 2) {
          window.location.href = this.ApiUrl + `/report/downloadDeliverReport.json` + param;
        }
        else {
          window.location.href = this.ApiUrl + `/report/downloadMaterialReport.json` + param;
        }
      },

      selectReportList() {
        if (this.modeType == 1) {
          this.getOrderReportList();
        }
        else if (this.modeType == 2) {
          this.getDeliverReportList();
        }
        else {
          this.getMaterialReportList();
        }
      },

      changePageNo(pageNo) {
        //this.pageSize = 10;
        this.pageNo = pageNo;
        this.selectReportList();

      },
      changePageSize(pageSize) {
        this.pageNo = 1;
        this.pageSize = pageSize;
        this.selectReportList();

      },

      getFilterData() {

        console.log(this.nameLike);
        this.pageSize = 10;
        this.pageNo = 1;
        //this.selectReportList();
        this.selectReport();
      },

      changeReport(data) {
        console.log(data);
        if (data == '订单报告') {
          this.modeType = 1;
          this.isOder = true;
          this.isAd = false;
          this.isMaterial = false;

          this.hit = '请输入订单名称';
        }
        else if (data == '广告报告') {
          this.modeType = 2;
          this.isOder = false;
          this.isAd = true;
          this.isMaterial = false;
          this.hit = '请输入广告名称';

        }
        else {
          this.modeType = 3;
          this.isOder = false;
          this.isAd = false;
          this.isMaterial = true;
          this.hit = '请输入物料名称';

        }
        console.log(this.modeType);
        this.nameLike = '';
        this.codeGroupType = '';
        this.codeId = '';
        this.codeGroupId = '';
        this.pageNo = 1;
        this.selectReport();

      },
      startTime(data) {
        if (data.length < 2 || data[0] == '' || data[1] == '') {
          this.$Message.error('请选择日期范围');
          return false;
        }
        this.startDay = data[0];
        this.endDay = data[1];
        this.pageNo = 1;
        this.selectReport();
      }
    }
  }
</script>



